import React from 'react'

export default function Itinerario() {
    return (
        <div className="pb-5 itinerario" >

            <h3 className="card-title text-center">ITINERARIO</h3><br/>   
            <h5><b>PUNTO DE CONCENTRACIÓN:</b></h5>
            <p className="card-text"><b>07:00 AM - </b> Plaza de armas de Ica</p>

            {/*DESCRIPCION ITINERARIO DIA 1*/}
            <p><b>DIA 1</b></p>

            <p className="card-text"><b>08:30 AM - </b>Partida desde La Plaza de armas de Ica, con movilidad turística</p>

            <p className="card-text"><b>10:00 AM - </b>Nos dirigimos hacia Ocucaje, donde haremos una parada rápida de 15 minutos para el uso de servicios higiénicos y la compra de algunas provisiones para el recorrido. Al estar listos abordaremos el bus con dirección al Cañón de los Perdidos</p>

            <p className="card-text"><b>11:00 AM - </b>Llegada al cañón, donde empezará nuestra travesía. A lo largo de la ruta encontraremos hermosos paisajes naturales que yacen a las orillas del río Callango, siguiendo la ruta del río seco llegaremos hacia el gran “Cañón de los Perdidos”, situado en Las Pampas de Gamonal. Durante el recorrido se les hará entrega de un box lunch de cortesía.</p>

            <p className="card-text"><b>01:00 PM - </b>Terminaremos nuestro recorrido y nos dirigimos al caserío de Ocucaje</p>

            <p className="card-text"><b>02:00 PM - </b>Al llegar a Ocucaje, les brindaremos a los pasajeros recomendaciones de restaurantes típicos locales. Tendrán la tarde libre para degustar vinos en la “Bodega Ocucaje” y disfrutar de un recorrido alrededor de la plaza de armas.</p>

            <p className="card-text"><b>05:00 PM - </b>Abordaremos la movilidad y nos dirigiremos a la ciudad de Nazca</p>

            <p className="card-text"><b>07:00 PM - </b>Los pasajeros llegarán a la bella ciudad de Nazca y tendrán la opción de dar un recorrido nocturno alrededor de la Plaza de Armas de Nazca o descansar en el hotel del largo viaje.</p>
            
            {/*DESCRIPCION ITINERARIO DIA 2*/}

            <br/><p><b>DIA 2</b></p>

            <p className="card-text"><b>07:30 AM - </b>Desayuno en el Hotel Mirador de Nazca</p>

            <p className="card-text"><b>08:00 AM - </b>Partida a la Duna Cerro Blanco.</p>

            <p className="card-text"><b>09:00 AM - </b>Llegada a la duna más grande de sur América. El recorrido comienza con una espectacular caminata de 2 horas aproximadamente, a través de las montañas que forman parte de la cadena de los Andes Peruanos.  Apreciamos los hermosos paisajes naturales hasta llegar a la cima, donde empezará la diversión y la adrenalina. De cortesía se les hará entrega de agua y/o bebidas energizantes durante la caminata.</p>

            <p className="card-text"><b>11:00 AM - </b>Llegada a cima de la Duna Cerro Blanco donde nuestro instructor de sandboarding impartirá las indicaciones, de cómo usar la tabla, los diferentes estilos y hará entregará los equipos respectivos a cada pasajero. Existen diferentes pendientes para todas las categorías, los más experimentados podrán deslizarse por la última pendiente que tiene una altura aproximada de 600 metros.</p>

            <p className="card-text"><b>Retorno a la ciudad de Nasca - </b>Al finalizar la aventura llegaremos a la orilla de la duna, donde emprenderemos una caminata de 30 minutos, hasta un punto en cual nos estará esperando la movilidad que nos traerá de regreso a la ciudad.</p>

            <p className="card-text"><b>01:30 PM - </b>Retorno a la Plaza de Armas de Nazca para que los pasajeros puedan degustar de los manjares de la tierra nazqueña.</p>

            <p className="card-text"><b>02:00 PM - </b>Tarde libre para realizar un recorrido alrededor de la Plaza de Armas de Nazca</p>

            <p className="card-text"><b>04:00 PM - </b>Nos despedimos de la bella ciudad de Nazca para abordar la movilidad turística y retornar a la plaza de armas de Ica</p>
        </div>
    )
}
