import React from 'react'

export default function Itinerario() {
    return (
        <div className="pb-5 itinerario" >
            
            <h3 className="card-title text-center">ITINERARIO</h3><br/>   
            <h5><b>PUNTO DE CONCENTRACIÓN:</b></h5>   
            <p className="card-text"><b>06:30 AM - </b> Plaza de armas de Ica</p>

            {/*DESCRIPCION ITINERARIO DIA 1*/}
            <p><b>DIA 1</b></p>

            <p className="card-text"><b>08:00 AM - </b>Llegada a la ciudad de Palpa, tierra de mangos, naranjas y ciruelos. Nuestros pasajeros tendrán tiempo para de probar las delicias palpeñas e interactuar con los pobladores</p>

            <p className="card-text"><b>09:00 AM - </b>Visita al Museo de Palpa, donde apreciamos una variedad de piezas arqueológicas encontradas en asentamientos pre-incas de la zona. El recorrido durará aproximadamente 30 minutos.</p>

            <p className="card-text"><b>09:30 AM - </b>Abordaremos la movilidad y nos dirigiremos a la ciudad Perdida de Huayuri, un asentamiento urbano prehispánico a las faldas de los cerros. El milenario Huarango nos recibirá en las puertas de la ciudad y empezaremos con la caminata, al final del recorrido observaremos artesanos que demuestran su arte a través de grabados en piedras, hay una variedad de recuerdos de Palpa que podrán adquirir.</p>

            <p className="card-text"><b>10:30 PM - </b>Nos dirigiremos a las Líneas de Palpa, que están a unos 30 minutos aproximadamente; durante el camino se hará entrega de un box lunch de cortesía a nuestros pasajeros. Las líneas de Palpa son las primeras representaciones de la ideología y la mística de las culturas Nazca y Paracas.</p>

            <p className="card-text"><b>12:30 PM - </b>Retorno a la Plaza de Armas de Palpa para que los pasajeros puedan degustar de los diferentes manjares palpeños, como los son el Ceviche de Langostinos, el escabeche de garbanzos, la causa de garbanzos, entre otros. Tendrán tiempo libre para comprar recuerdos y degustar los dulces de Palpa.</p>

            <p className="card-text"><b>02:30 PM - </b>Emprenderemos el camino hacia el Sitio Arqueológico de La Muña</p>

            <p className="card-text"><b>03:00 PM - </b>Al llegar observamos los vestigios que albergaron a las culturas Nazca y Paracas. El arqueólogo peruano Johny Isla descubrió la tumba real del Señor de Palpa y numerosas figuras trazadas en las pampas de este valle que tenían fines ceremoniales a los dioses de dichas culturas. El recorrido será de una hora aproximadamente</p>

            <p className="card-text"><b>04:30 PM - </b>Nos dirigiremos como punto final del recorrido del día, Llipata. Después de una pequeña caminata encontraremos al mirador, donde se apreciará las diferentes representaciones ceremoniales y divinidades de la cultura Paracas ubicadas en las faldas de los cerros.</p>

            <p className="card-text"><b>05:30 PM - </b>Abordaremos la movilidad y nos dirigiremos hacia la ciudad de Nazca</p>

            <p className="card-text"><b>06:30 PM - </b>Llegada a la ciudad de Nazca, los pasajeros se alojarán en el Hotel Mirador de Nazca, ubicado al frente de la plaza de armas de Nazca</p>

             {/*DESCRIPCION ITINERARIO DIA 2*/}   
            <br/><p><b>DIA 2</b></p>

            <p className="card-text"><b>08:00 AM - </b>Desayuno en el Hotel</p>

            <p className="card-text"><b>09:00 AM - </b>Visita al Museo  Arqueológico Antonini. Contiene gran parte de los hallazgos recuperados por la misión italiana denominada Proyecto Nasca 1982-2011. Todo el material expuesto pertenece a un contexto arqueológico bien documentado y explicado a los visitantes</p>

            <p className="card-text"><b>10:00 AM - </b>Rápidamente nos dirigiremos a Los Paredones, un antiguo centro administrativo inca ubicado en las laderas del cerro Los Altos de Nazca</p>

            <p className="card-text"><b>11:00 AM - </b>Visitaremos también una de las obras de ingeniería hidráulica más destacadas del mundo, los Acueductos de Cantalloc se construyeron en medio del desierto por la antigua cultura Nazca. Son un total de 46 acueductos subterráneos, de los cuales 32 siguen en funcionamiento en la actualidad por los pobladores de Nazca.</p>

            <p className="card-text"><b>12:00 pm - </b>Debido a la cercanía de los centros arqueológicos retornaremos temprano para el mejor momento, disfrutar de un almuerzo tradicional, les brindaremos las opciones más representativas para su elección</p>

            <p className="card-text"><b>02:00 PM - </b>Tarde libre para realizar un recorrido alrededor de la Plaza de Armas de Nazca  y comprar recuerdos.</p>

            <p className="card-text"><b>03:00 PM - </b>Iniciamos la partida hacia las Líneas de Nazca. Estos geoglifos colombinos fueron declarados como Patrimonio de la Humanidad.  Existen alrededor de 300 figuras distintas, que incluyen animales y plantas. Están compuestos por más de 10,000 líneas, algunas de estas de 30 metros de ancho y más de 9 kilómetros de largo. Las líneas se pueden visualizar de dos maneras diferentes, a través del mirador y de un recorrido en avioneta. El guía les dará una explicación en el mirador, pero de manera totalmente libre pueden tomar la opción de abordar una avioneta.</p>

            <p className="card-text"><b>05:00 PM - </b>Abordaremos la movilidad turística para retornar a la ciudad de Ica</p>

            <p className="card-text"><b>07:00 PM - </b>Llegaremos al punto de retorno, la plaza de armas de Ica</p>
        </div>
    )
}
